.container-std {
  height: 100vh;
  width: 100vw;
  outline-offset: -7vh;
}

.container {
  position: relative;
  min-width: 100%;
}
.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(12, 1fr);
  grid-gap: 1px;
  padding: 1px;
  height: 100vh;
}

