.picture img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 111%;
    display: block;
}

@media (max-width: 768px) {
    .picture {
        visibility: hidden;
    }
}

@media (max-width: 575.98px) {
    .picture {
        visibility: hidden;
    }
}
